<template>
    <el-container>
        <el-aside class="left-group-aside" v-show="$getUserGroupSee()">
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <toolbar
                :buttonList="buttonList"
                @callFunction="callFunction"
                placeholder="用户名、负责项目、角色、单位、手机过滤"
            ></toolbar>
            <!--列表-->
            <el-table
                :data="users"
                v-loading="listLoading"
                :row-class-name="$tableRowClassName"
                style="width: 100%"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="60">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column prop="username" label="用户名" min-width="2">
                </el-table-column>

                <el-table-column
                    prop="siteshortname"
                    label="负责项目"
                    min-width="2"
                >
                    <template slot-scope="scope">
                        <div v-if="scope.row.usersitetype == 0">全部项目</div>
                        <div
                            v-else-if="scope.row.usersitetype == 1"
                            v-text="scope.row.siteshortname"
                        ></div>
                        <div v-else>多个项目</div>
                    </template>
                </el-table-column>
                <el-table-column prop="rolename" label="角色" min-width="2">
                </el-table-column>
                <!-- <el-table-column prop="position" label="职位" min-width="2">
                </el-table-column> -->
                <el-table-column prop="company" label="单位" min-width="2">
                </el-table-column>
                <el-table-column
                    prop="mobile"
                    label="手机"
                    min-width="2"
                ></el-table-column>
                <!-- <el-table-column prop="company" label="单位" min-width="2">
                </el-table-column> -->
                <el-table-column prop="status" label="状态" min-width="3">
                    <template slot-scope="scope">
                        <el-switch
                            v-model="scope.row.status"
                            :active-value="1"
                            active-text="正常"
                            :inactive-value="0"
                            inactive-text="冻结"
                            @change="
                                handleStatus($event, scope.$index, scope.row)
                            "
                        ></el-switch>
                    </template>
                </el-table-column>
                <el-table-column
                    label="操作"
                    v-if="canEdit || canDelete || canReset"
                    min-width="4"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)"
                            icon="el-icon-edit"
                        ></el-button>
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="mini"
                            @click="handleDel(scope.$index, scope.row)"
                            icon="el-icon-delete"
                        ></el-button>
                        <el-tooltip
                            class="item"
                            effect="dark"
                            content="重置密码"
                            placement="top"
                            :enterable="false"
                            :open-delay="500"
                        >
                            <el-button
                                v-if="canReset"
                                plain
                                type="success"
                                size="mini"
                                @click="handleReset($event, scope.row)"
                                icon="el-icon-refresh"
                            ></el-button>
                        </el-tooltip>
                        <el-tooltip
                            class="item"
                            effect="dark"
                            content="绑定监测对象"
                            placement="top"
                            :enterable="false"
                            :open-delay="500"
                        >
                            <el-button
                                v-if="canSite"
                                plain
                                type="primary"
                                size="mini"
                                @click="handleSite($event, scope.row)"
                                icon="el-icon-link"
                            ></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="editForm"
                    label-width="120px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="用户名" prop="username">
                                <el-input
                                    v-model="editForm.username"
                                    auto-complete="off"
                                    maxlength="20"
                                ></el-input>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item label="角色" prop="ROLEID">
                                <el-select
                                    v-model="editForm.ROLEID"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in roles"
                                        :key="item.ROLEID"
                                        :label="item.rolename"
                                        :value="item.ROLEID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item> </el-col
                    ></el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="监测对象分组" prop="GROUPID">
                                <el-input
                                    v-model="editForm.groupshortname"
                                    auto-complete="off"
                                    disabled
                                    v-if="isEdit"
                                ></el-input>
                                <el-select
                                    v-model="editForm.GROUPID"
                                    filterable
                                    placeholder="请选择"
                                    @change="groupChange"
                                    v-else
                                >
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="item.GROUPID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="手机" prop="mobile">
                                <el-input
                                    v-model="editForm.mobile"
                                    auto-complete="off"
                                    maxlength="11"
                                ></el-input>
                            </el-form-item> </el-col
                    ></el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item
                                label="负责项目范围"
                                prop="usersitetype"
                            >
                                <el-select
                                    v-model="editForm.usersitetype"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in usersitetypes"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item
                                label="监测对象"
                                prop="SITEID"
                                v-if="editForm.usersitetype == 1"
                            >
                                <el-select
                                    v-model="editForm.SITEID"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in selectSites"
                                        :key="item.SITEID"
                                        :label="item.siteshortname"
                                        :value="item.SITEID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item> </el-col
                    ></el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="单位" prop="company">
                                <el-input
                                    v-model="editForm.company"
                                    auto-complete="off"
                                    maxlength="30"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="职位" prop="position">
                                <el-input
                                    v-model="editForm.position"
                                    auto-complete="off"
                                    maxlength="30"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="editFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import {
    getFrontUserPage,
    getGroupFrontUser,
    getFrontRoleAll,
    deleteFrontUser,
    editFrontUser,
    addFrontUser,
    switchFrontUser,
    resetPwd,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
export default {
    components: { leftgroup, Toolbar },
    mixins: [dicmixin],
    data() {
        return {
            groupCounts: [],
            buttonList: [],
            filters: {
                name: "",
            },
            users: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                username: [
                    {
                        required: true,
                        message: "请填写用户名",
                        trigger: "blur",
                    },
                ],
                ROLEID: [
                    {
                        required: true,
                        message: "请选择角色",
                        trigger: "blur",
                    },
                ],
                usersitetype: [
                    {
                        required: true,
                        message: "请选择负责项目范围",
                        trigger: "blur",
                    },
                ],
            },
            editForm: {},
            canEdit: false,
            canDelete: false,
            canStatus: false,
            canReset: false,
            canSite: false,
            groups: [],
            selectSites: [],
            selectGroupId: 0,
            isEdit: false,
            roles: [],
            usersitetypes: [
                { id: 0, name: "全部项目" },
                { id: 1, name: "单个项目" },
                { id: 2, name: "多个项目" },
            ],
        };
    },
    methods: {
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getUserList();
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getUserList(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let paras = {
                GROUPID: this.selectGroupId,
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getFrontUserPage(paras).then((res) => {
                if (res.success) {
                    this.users = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.getUserList(val);
        },
        getGroupCounts() {
            getGroupFrontUser().then((res) => {
                if (res.success) {
                    this.groupCounts = res.data;
                }
            });
        },
        getRoles() {
            getFrontRoleAll().then((res) => {
                if (res.success) {
                    this.roles = res.data.filter((item) => {
                        return item.ROLEID > 1;
                    });
                }
            });
        },
        groupChange(val) {
            this.getGroupSites(val);
            if (!this.isEdit) {
                this.$set(this.editForm, "SITEID", "");
            }
            this.selectSites = this.groupSites;
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.USERID };
                    deleteFrontUser(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getUserList(this.page);
                        this.getGroupCounts();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        async handleEdit(index, r) {
            this.isEdit = true;
            this.selectSites = [];
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }
            if (row.SITEID == 0) {
                row.SITEID = null;
            }
            this.groupChange(row.GROUPID);
            this.editFormVisible = true;
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.selectSites = [];
            this.editFormVisible = true;
            this.editForm = {};
            this.isEdit = false;
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    if (para.usersitetype != 1) {
                        para.SITEID = 0;
                    }
                    if (this.isEdit) {
                        editFrontUser(para).then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: "修改成功",
                                    type: "success",
                                });
                                this.$refs["editForm"].resetFields();
                                this.editFormVisible = false;
                                this.getUserList(this.page);
                                this.getGroupCounts();
                            } else {
                                this.$message({
                                    message: res.msg,
                                    type: "error",
                                });
                            }

                            this.editLoading = false;
                        });
                    } else {
                        addFrontUser(para).then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: "新建成功",
                                    type: "success",
                                });
                                this.$refs["editForm"].resetFields();
                                this.editFormVisible = false;
                                this.getUserList(this.page);
                                this.getGroupCounts();
                            } else {
                                this.$message({
                                    message: res.msg,
                                    type: "error",
                                });
                            }

                            this.editLoading = false;
                        });
                    }
                }
            });
        },
        //冻结解冻
        handleStatus($event, index, row) {
            let para = { USERID: row.USERID, status: $event };
            switchFrontUser(para).then((res) => {
                if (res.success) {
                    this.$message({
                        message: "更新成功",
                        type: "success",
                    });
                    this.getUserList(this.page);
                    this.getGroupCounts();
                } else {
                    // let r = row;
                    // r.status = row.status == 0 ? 1 : 0;
                    // this.Groups[index] = r;
                    this.$message({
                        message: "更新失败",
                        type: "error",
                    });
                    this.getUserList(this.page);
                    this.getGroupCounts();
                }
            });
        },
        //重置密码
        handleReset(e, row) {
            let target = e.target;
            let para = { USERID: row.USERID };
            this.$confirm("确认重置密码吗?", "提示", {
                type: "warning",
            }).then(() => {
                resetPwd(para).then((res) => {
                    if (res.success) {
                        this.$message({
                            message: "重置成功",
                            type: "success",
                        });
                        // this.getUserList();
                        // this.getGroupCounts();
                    } else {
                        this.$message({
                            message: "重置失败",
                            type: "error",
                        });
                        // this.getUserList();
                        // this.getGroupCounts();
                    }
                });
            });
            target.blur();
        },
        //绑定监测对象
        async handleSite(index, r) {
            this.$router.replace(`/frontuser/site/${r.USERID}`);
        },
    },
    mounted() {
        this.getRoles();
        this.getGroups();
        this.getGroupCounts();
        this.getUserList();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else if (element.Func == "handleStatus") {
                this.canStatus = true;
            } else if (element.Func == "handleReset") {
                this.canReset = true;
            } else if (element.Func == "handleSite") {
                this.canSite = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-select {
    width: 100%;
}
.el-button--mini {
    font-size: 16px;
    font-weight: 900;
}
</style>


